import React from "react"
import ToolTip from "./tooltip";


class CircleIcon extends React.Component {

    constructor(props) {
        super()
        this.state = {
            showTip: false,
            position: undefined
        }
        this.mouseOverHandler = this.mouseOverHandler.bind(this);
        this.mouseOutHandler = this.mouseOutHandler.bind(this);

    }

    mouseOverHandler(evt) {
        this.setState({ showTip: true });
        this.setState({ position: evt.pageY - 100 });
        console.log(this.state.position)
    }

    mouseOutHandler() {
        this.setState({ showTip: false });
    }



    render() {



        return (
            <div>
                <div style={{
                    width: '30px',
                    height: '30px',
                    background: '#000',
                    borderRadius: '50%',
                    textAlign: 'center',
                    fontFamily: 'Open Sans',
                    fontWeight: '400',
                    fontSize: '35px',
                    color: '#fff',
                    paddingTop: '1px',
                    margin: '0',
                    cursor: 'pointer',
                    ':hover': {
                        color: '#000',
                        background: '#fff'
                    }
                }}

                    onMouseOver={(evt) => { this.mouseOverHandler(evt); }}
                    onMouseOut={(evt) => { this.mouseOutHandler() }}>
                    <p css={{ margin: '0' }}>+</p>
                </div>
                <div>
                    <ToolTip showTip={this.state.showTip} position={this.state.position} />
                </div>

            </div>

        )
    }

}






export default CircleIcon