import React from "react"
import PropTypes from "prop-types"
import PageHeader from "./pageinfoheader"
import PageDescription from "./pageinfodesc"

const PageInfo = ({ header, decscription, color, marginTop, width, mobileMarginTop, mobileWidth }) => (

    <div css={{
        marginTop: marginTop,
        marginLeft: '10%',
        width: width,
        '@media(max-width:400px)': {
            marginTop: mobileMarginTop,
            width: '200px'
        }


    }}>
        <PageHeader header={header} color={color} />
        <PageDescription decscription={decscription} color={color} />
    </div>
)

PageInfo.propTypes = {
    header: PropTypes.string,
    decscription: PropTypes.string
}


PageInfo.defaultProps = {
    color: '#ffff',
    marginTop: '21%',
    mobileMarginTop: '71%',
    mobileWidth: '200px',
    width: '255px'
}


export default PageInfo