import React from "react"



export default class GiantHeader extends React.Component {

    render() {
        return (
            <h1 css={{
                fontFamily: "Open Sans",
                fontWeight: '700',
                fontSize: '250px',
                textTransform: 'uppercase',
                WebkitTextStroke: this.props.stroke,
                textStroke: this.props.stroke,
                color: this.props.color,
                opacity: this.props.opacity
            }}>{this.props.text}</h1>)
    }



}
